.panel{
    display: grid;
    grid-template-columns: minmax(calc(25vw + 25vh), 1fr);
    grid-template-rows: 5vh;
    justify-content: center;
    justify-items: center;    
    padding: calc(0.25vw + 0.25vh) 0vw;
    width: 50vw;
    height: calc(20vmin + 10vh);
    font-size: calc(1vw + 1vh);
}

