    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
}

.panel > ul{
    list-style: none;
    margin: 0px;
    padding: 0px;
    font-size: calc(0.8vw + 0.8vh);
}

.subpanel{
    display: grid;
    grid-template-columns: calc(7.5vw + 7.5vh) calc(7.5vw + 7.5vh) calc(7.5vw + 7.5vh);
    grid-template-rows: calc(1.5vw + 1.5vh) calc(1.5vw + 1.5vh) calc(1.5vw + 1.5vh) calc(1.5vw + 1.5vh);
