  width: 15vw;
  min-height: 17vh;    
}

.smoniterP1 {
  grid-area: p1;
  text-align: center;
}

.smbtn {
  font-size: 1vw;
  margin: 0.25vw;  
  border-radius: 0.5vw;
}

.smoniterB1 {
  grid-area: b1;
}

.smoniterB2 {
  grid-area: b2;
}

.smoniterB3 {
  grid-area: b3;
}

.sbox {
  display: flex;
  flex-flow: column-reverse;
  border-style: solid;  
  border-width: 0.4vw;
  border-top: 0;
  border-radius: 0.2vw;
  width: 8vw;
  height: 25vw;
  margin: 2vw;
}

.stackable {
  display: flex;
  flex-direction: row;               
  justify-content: center;
  align-items: center;  
  border-style: solid;
  border-width: 1px;
  border-radius: 0.2vw;
  width: 7.9vw;
  height: 1vw;
  font-size: 0.8vw;
  -webkit-animation: fadeOut ease 8s;
          animation: fadeOut ease 8s;
}

.nstack {
  background: repeating-linear-gradient(
    45deg,
    #344f85,
    #606dbc 10px,
    #465298 10px,
    #465298 20px
  );
}

.drop-in {
  -webkit-animation: dropIn 1s 0s cubic-bezier(.32,.96,.47,.95) forwards;
          animation: dropIn 1s 0s cubic-bezier(.32,.96,.47,.95) forwards;
}

@-webkit-keyframes dropIn{
  from {
      -webkit-transform: translate(-30px, -100px);
              transform: translate(-30px, -100px)
  }
  to {
      -webkit-transform: translate((0px, 0px));
              transform: translate((0px, 0px))
  }
}

@keyframes dropIn{
  from {
      -webkit-transform: translate(-30px, -100px);
              transform: translate(-30px, -100px)
  }
  to {
      -webkit-transform: translate((0px, 0px));
              transform: translate((0px, 0px))
  }
}
